import React, { Component } from 'react';
import { Slide } from 'react-slideshow-image';
import img1 from '../images/img1.jpg';
import img2 from '../images/img2.jpg';
import img3 from '../images/img3.png';
import '../css/slide.css';
import '../css/style.css';

const slideImages = [
    img1,
    img2,
    img3
  ];

  const properties = {
    duration: 5000,
    transitionDuration: 500,
    infinite: true,
    indicators: true,
    arrows: true,
    pauseOnHover: true,
    onChange: (oldIndex, newIndex) => {
      console.log(`slide transition from ${oldIndex} to ${newIndex}`);
    }
  }

const Slideshow = () => {
    return (
      
      <div className="slide-container">
        <Slide {...properties}>
          <div className="each-slide">
            <div style={{'backgroundImage': `url(${slideImages[0]})`}}>
                <div className='slide_wrap'>
                    <p className='v_tit'>끈기있는 개발자</p>
                    <p className='v_txt'>어려움이 있는 상황에서도 좌절하지 않고 끈기있게 </p>
                    <button className='btn01'>더 알아보기</button>
                </div>
            </div>
          </div>
          <div className="each-slide">
            <div style={{'backgroundImage': `url(${slideImages[1]})`}}>
                <div className='slide_wrap'>
                    <p className='v_tit'>소통하는 개발자</p>
                    <p className='v_txt'>고객사와의 원활한 의사소통을 통해 요구사항을 정확히 파악하여 업무에 있어</p>
                    <button className='btn01'>더 알아보기</button>
                </div>
            </div>
          </div>
          <div className="each-slide">
            <div style={{'backgroundImage': `url(${slideImages[2]})`}}>
                <div className='slide_wrap'>
                    <p className='v_tit'>경험많은 개발자</p>
                    <p className='v_txt'>다양하고 폭넓은 개발 경험을 통해</p>
                    <button className='btn01'>더 알아보기</button>
                </div>
            </div>
          </div>
        </Slide>
      </div>
    )
}

class MainPage extends Component{
  render(){
    return(
      <div className='main_bg'>
        <div className='main_wrap'>
          <ul>
            <li>
              <a href='#'>
                <div className='h_wrap'></div>
                <div className='h_img01'> 
                  <p>Language</p> 
                </div>
                <div className='h_tit'>
                  <ul className='txt_ul'>
                    <li><span></span>Java</li>
                    <li><span></span>JavaScript</li>
                    <li><span></span>SQL</li>
                  </ul>
                </div>
              </a>
            </li>
            <li>
              <a href='#'>
              <div className='h_wrap'></div>
                <div className='h_img02'>
                  <p>Framework</p>
                </div>
                <div className='h_tit'>
                  <ul className='txt_ul'>
                    <li><span></span>Java</li>
                    <li><span></span>JavaScript</li>
                    <li><span></span>SQL</li>
                  </ul>
                </div>
              </a>
            </li>
            <li>
              <a href='#'>
              <div className='h_wrap'></div>
                <div className='h_img03'>
                  <p>DataBase</p>
                </div>
                <div className='h_tit'>
                  <ul className='txt_ul'>
                    <li><span></span>Java</li>
                    <li><span></span>JavaScript</li>
                    <li><span></span>SQL</li>
                  </ul>
                </div>
              </a>
            </li>
            <li>
              <a href='#'>
              <div className='h_wrap'></div>
                <div className='h_img04'>
                  <p>OS</p>
                </div>
                <div className='h_tit'>
                  <ul className='txt_ul'>
                    <li><span></span>Java</li>
                    <li><span></span>JavaScript</li>
                    <li><span></span>SQL</li>
                  </ul>
                </div>
              </a>
            </li>
          </ul>   
        </div>
      </div>
    );
  }
}

class Main extends Component{
    render(){
        return (
            <div>
                <Slideshow></Slideshow>
                <MainPage></MainPage>
            </div>
        );
    }
}

export default Main;