import React, { Component } from 'react';
import menu from '../data/data';

class Gnb extends Component{ 
    render(){
        var menu_list = [];
        var i=0;
        console.log(this.props.menu)
        while(i<this.props.data.menu.length){
            var data = this.props.data.menu[i];
            console.log(data.id)
            if(this.props.menu.mode === data.id){
                var val  = <li key={data.id} className='active'><a href={data.fileName}>{data.title}</a></li>;
            }else{
                var val  = <li key={data.id}><a href={data.fileName}>{data.title}</a></li>;
            }
            
            menu_list.push(val);
            i++;
        }
        return (
            <div>
            <ul className='top_menu' >   
                {menu_list}
            </ul>
            <ul className='m_menu' >   
                {menu_list}
            </ul>
            </div>
        );
    }
}

class Header extends Component{  
    state = {
        'mode' : 'menu_01'
    }
    render(){
        return(
            <header>
                <div id='header'>
                    <div className='header_top'>
                        <p><a href='/'>YARA PAGE</a></p>
                        <Gnb data={menu} menu={this.state}></Gnb>
                    </div>
                </div>
            </header>
        );
    }
}

export default Header;